.stars {
  display: flex;

  &__star {
    background: url("../images/star.svg") no-repeat center center;
    width: 16px;
    height: 16px;
    margin-left: 4px;
  }
  
  &__star:first-child {
    margin-left: 0;
  }
  
  .stars--1 > &__star:nth-child(-n+1),
  .stars--2 > &__star:nth-child(-n+2),
  .stars--3 > &__star:nth-child(-n+3),
  .stars--4 > &__star:nth-child(-n+4),
  .stars--5 > &__star:nth-child(-n+5) {
    background: url("../images/star-active.svg") no-repeat center center;
  }
}
